.loader {
  z-index: 99999;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .75);
  display: none;
  position: fixed;
  top: 0;
  left: 0;
}

.display {
  justify-content: center;
  align-items: center;
  display: flex;
}

.loader__text {
  color: #b92f2c;
  margin-left: 5px;
  font-family: inherit;
  display: inline-block;
}

.loader__bounceball {
  height: 37px;
  width: 15px;
  display: inline-block;
  position: relative;
}

.loader__bounceball:before {
  content: "";
  width: 15px;
  height: 15px;
  transform-origin: 50%;
  background-color: #b92f2c;
  border-radius: 50%;
  animation: bounce .5s infinite alternate;
  display: block;
  position: absolute;
  top: 0;
}

@keyframes bounce {
  0% {
    height: 5px;
    border-radius: 60px 60px 20px 20px;
    top: 30px;
    transform: scaleX(2);
  }

  35% {
    height: 15px;
    border-radius: 50%;
    transform: scaleX(1);
  }

  100% {
    top: 0;
  }
}

